.Logo {
    height: 100px; 
    width: 100px; 
    position: absolute; 
    left: 10px; 
    display: flex; 
    justify-content: center;
    padding: 10px; 
    align-self: center;   
    margin-top: -10px; 

    img {
        max-width: 100%; 
        max-height: 100%; 
    }

    &:hover {
        cursor: pointer; 
    }
}